

.fullscreen2{
    background-image: url("/images/section_bg03.jpg");
    background-size: cover;
    width: 100%;
    height: 300px;
    background-repeat: no-repeat;
     display: flex;
     justify-content: center;
     align-items: center;
}

.fullscreen2 .os {
