}
.fullscreen2 .list {display: flex;margin-left: 10px;}

.fullscreen2 .list li:first-child a{color: white;}

.fullscreen2 .list li:first-child a:hover{text-decoration: none;}

.fullscreen2 .list li:last-child {color: gainsboro;}